.menuPlaceholder {
    height: 64px;
}

.menuBar {
    /*background-color: #222;*/
    background-color: #563d7c;
    height: 64px;
    color: #fff;
    position: fixed;
    width: 100%;
    z-index: 10;
    white-space: nowrap;
}

.menuInner {
    margin: 0 69px; /* 64 (menu size) - 15 (text padding) + 20 (section padding) */
    padding-top: 14px;
}

.menuInnerRight {
    position: absolute;
    right: 0;
    top: 0;
    padding-right: 69px;
    background: #563d7c;
    padding-top: 14px;
    z-index: 2;
}

.menuBtn {
    /*
    background: #222;
    font-weight: bold;
    */

    padding: 8px 15px;
    cursor: pointer;
    display: inline-block;
    background: transparent;
    font-size: 16px;
    color: #cbbde2;
}

.menuBtn:hover {
    color: #fff;
}

.selectedBtn {
    /*
    background: #fff;
    color: #000;
    */
    background: transparent;
    color: #fff;
}

.logoSpan {
    padding: 8px 15px;
    width: 30px;
    position: relative;
    display: inline-block;
}

.logoImg {
    position: absolute;
    height: 30px;
    /*width: 30px;*/
    bottom: -10px;
    cursor: pointer;
}


