

.bodyCnt {
  max-width:800px;
  padding:40px 40px 0px 40px;
  margin:auto;
}

.postCnt {
  margin-bottom:50px;
}

.postContent {
  background:#fff;
  border:1px solid #ccc;
  padding:10px;
}

.newPostText {
  background:#fff;
  border:2px solid #56c;
  padding:10px;
  display:block;
  width:calc(100% - 25px);
  font-size: 16px;
}

.newPostButton {
  margin-top:5px;
  background:#56c;
  border:#ccc;
  padding: 8px 15px;
  font-weight: bold;
  color:#fff;
  font-size: 16px;
  cursor:pointer;
}

.gap {
  padding-top:40px;
}

body {
  /*
  background: url('/background.jpg') no-repeat center center fixed;
  background-color: #bbaa99;
  */
  background-color: #eeeeee;
  background-size: cover;
  -o-background-size: cover;
}


